import uniSegmentedControl from '@/components/uni-segmented-control/uni-segmented-control.vue'
import uCharts from '@/components/u-charts/u-charts.js';

var canvasObj = {};
var _self;
export default {
	components: {
		uniSegmentedControl
	},
	data() {
		return {
			items: ['选项卡1', '选项卡2', '选项卡3'],
			current: 0,
			activeColor: '#007aff',
			pie: {
				"series": [{
					"name": "一班",
					"key": "one",
					"data": 50
				}, {
					"name": "二班",
					"key": "two",
					"data": 30
				}, {
					"name": "三班",
					"key": "three",
					"data": 20
				}, {
					"name": "四班",
					"key": "four",
					"data": 18
				}, {
					"name": "五班",
					"key": "five",
					"data": 8
				}]
			},
			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			title: 'Hello'
		}
	},
	onLoad() {
		let date = new Date();
		let year = date.getFullYear();
		let month = date.getMonth() + 1;
		let day = date.getDate();
		this.selectYear = year;
		this.selectMonth = month;
		this.selectDate = `${year}-${month}-${day}`;
		console.log(date.getDate());
		_self = this;
		this.cWidth = uni.upx2px(750);
		this.cHeight = uni.upx2px(500);
		this.showRing("canvasRing", this.pie);
	},
	methods: {
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index
			}
		},
		bindDateChange(e) {
			console.log(e);
			console.log(e.target.value);
			this.selectDate = e.target.value;
			var dateStrs = this.selectDate.split("-");
			this.selectYear = parseInt(dateStrs[0], 10);
			this.selectMonth = parseInt(dateStrs[1], 10);
		},
		showPie(canvasId, chartData) {
			canvasObj[canvasId] = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'pie',
				fontSize: 11,
				padding: [15, 15, 0, 15],
				legend: {
					show: true,
					padding: 5,
					lineHeight: 11,
					margin: 0,
				},
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				animation: false,
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				dataLabel: true,
				extra: {
					pie: {
						lableWidth: 15
					}
				},
			});
		},
		showRing(canvasId, chartData) {
			canvasObj[canvasId] = new uCharts({
				$this: _self,
				canvasId: canvasId,
				type: 'ring',
				fontSize: 11,
				padding: [5, 5, 5, 5],
				legend: {
					show: true,
					position: 'right',
					float: 'center',
					itemGap: 10,
					padding: 5,
					lineHeight: 26,
					margin: 5,
					//backgroundColor:'rgba(41,198,90,0.2)',
					//borderColor :'rgba(41,198,90,0.5)',
					borderWidth: 1
				},
				title: {
					name: '70%',
					color: '#7cb5ec',
					fontSize: 25 * _self.pixelRatio,
				},
				subtitle: {
					name: '收益率',
					color: '#666666',
					fontSize: 15 * _self.pixelRatio,
				},
				extra: {
					pie: {
						lableWidth: 15,
						ringWidth: 40 * _self.pixelRatio, //圆环的宽度
						offsetAngle: 0 //圆环的角度
					}
				},
				background: '#FFFFFF',
				pixelRatio: _self.pixelRatio,
				series: chartData.series,
				animation: false,
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				disablePieStroke: true,
				dataLabel: true,
			});

		},
		touchPie(e, id) {
			var currentItem;
			canvasObj[id].showToolTip(e, {
				format: function(item) {
					currentItem = item;
					return item.name + ':' + item.data
				}
			});
			console.log(currentItem);
		}
	}
}
